@import url(https://fonts.googleapis.com/css2?family=Playfair+Display&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  box-sizing: border-box;
  font-family: 'Playfair Display', serif;
}

.project-div{
  background-color: rgb(245, 245, 227);
  color: rgb(207, 167, 92);
  width: 100%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 50px;
}

.project-title{
  font-size: 80px;
  margin-bottom: 50px;
}

.cv-div{
  border: 1px rgb(207, 167, 92) solid;
  padding: 50px;
  width: 65%;
  display: flex;
  flex-direction: column;
}

/*  TOP */
.top-cv-div{
  border: 1px rgb(207, 167, 92) solid;
  margin: 30px 0;
  padding: 20px 15px;
}

/*  MAIN */
.main-cv-div{
  display:flex;
  flex-direction: row;
  justify-content: space-between;
}
.entry-title{
  font-size: 45px;
  text-shadow: 3px 3px rgba(216, 199, 164, 0.671);
}
button{
  background-color: rgb(207, 167, 92);
  color: white;
  border: none;
  box-shadow: 3px 3px rgb(216, 199, 164);
  transition: 0.1s all linear
}
button:hover{
  box-shadow: 2px 2px rgb(216, 199, 164);
  transform: translate(1px, 1px);
}
button:active{
  border: none;
  outline: none;
}
/*  LEFT */
.left-cv-div{
  width: 30%;
}
.contactBox{
  margin: 25px 0;
}
.contactBox-div{
  display: flex;
  flex-direction: row;
  align-items: center;
  margin: 8px 0;
  
}


/*  RIGHT */
.right-cv-div{
  width: 65%;
}

.dateHolder{
  display: flex;
  flex-direction: row;
}



/*  TOGGLE */
.toggle{
  position: absolute;
  top: 5%;
  right: 12%;
  font-size: 25px;
}


input{
  border:none;
  outline: none;
  font-size: 25px;
  background-color: rgba(231, 197, 134, 0.192);
  color:rgb(207, 167, 92);
  text-decoration: underline;
}

textarea{
  border:none;
  outline: none;
  font-size: 25px;
  background-color: rgba(231, 197, 134, 0.192);
  color:rgb(207, 167, 92);
  text-decoration: underline;
  min-height: 80px;
  min-width: 95%;
}
